.contact{
  min-height: calc( 100vh - var(--footer-height));
  display: flex;
  flex-direction: column;
  gap: 2em;
  align-items: center;
  justify-content: center;
}

.contact a{
  text-decoration: none;
  width: auto;
  background: linear-gradient(#2121216d, #2121216d);
  border: 3px double var(--secondary-text);
  border-radius: 25px;
  font-size: 1.75em;
  font-weight: 500;
  letter-spacing: 4px;
  color: var(--secondary-text);
  padding: 0.75em;
  cursor: pointer;
}
.contact a:hover{
  animation: smooth-fill 0.22s ease;
  animation-fill-mode: forwards;
}

.contact a:active{
  border-color: var(--tertiary-text);
  color: var(--tertiary-text);
}

.contact p{
  font-size: 2em;
}
@keyframes smooth-fill {
  10%{
    background: linear-gradient(90deg,#212121,#2121216d,#2121216d,#2121216d,#2121216d,#2121216d,#2121216d,#2121216d,#2121216d,#2121216d)
  }
  20%{
    background: linear-gradient(90deg,#212121,#212121,#2121216d,#2121216d,#2121216d,#2121216d,#2121216d,#2121216d,#2121216d,#2121216d)
  }
  30%{
    background: linear-gradient(90deg,#212121,#212121,#212121,#2121216d,#2121216d,#2121216d,#2121216d,#2121216d,#2121216d,#2121216d)
  }
  40%{
    background: linear-gradient(90deg,#212121,#212121,#212121,#212121,#2121216d,#2121216d,#2121216d,#2121216d,#2121216d,#2121216d)
  }
  50%{
    background: linear-gradient(90deg,#212121,#212121,#212121,#212121,#212121,#2121216d,#2121216d,#2121216d,#2121216d,#2121216d)
  }
  60%{
    background: linear-gradient(90deg,#212121,#212121,#212121,#212121,#212121,#212121,#2121216d,#2121216d,#2121216d,#2121216d)
  }
  70%{
    background: linear-gradient(90deg,#212121,#212121,#212121,#212121,#212121,#212121,#212121,#2121216d,#2121216d,#2121216d)
  }
  80%{
    background: linear-gradient(90deg,#212121,#212121,#212121,#212121,#212121,#212121,#212121,#212121,#2121216d,#2121216d)
  }
  90%{
    background: linear-gradient(90deg,#212121,#212121,#212121,#212121,#212121,#212121,#212121,#212121,#212121,#2121216d)
  }
  100%{
    background: linear-gradient(90deg,#212121,#212121,#212121,#212121,#212121,#212121,#212121,#212121,#212121,#212121)
  }
}