.expertise{
  min-height: 100vh;
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.expertise a{
  display: inline-block;
  text-decoration: none;
  pointer-events: none;
}
.credential{
  transform: translateX(100%);
  transition: transform 0.75s ease-out, filter 0.75s linear, border 0.2s ease-in;
  border: 3px solid transparent;
  filter: opacity(0%);
  border-radius: 25px;
  pointer-events: auto;
}
.credential p{
  color: var(--primary-text);
  font-size: clamp(0.9em, 2vw, 1.3em);
  margin: 0.25em 0.75em 0.25em 0;
}
.credential.even p{
  margin-left: 0.75em;
  margin-right: 0;
}
.even{
  transform: translateX(-100%);
}
.slide{
  transform: translateX(0);
  filter: opacity(100%);
}
.credential:hover{
  border: 3px solid var(--secondary-text);
}

.credential:active{
  transition: border 0s linear;
  border-color: var(--tertiary-text);
}