header{
  width: 100%;
}
nav{
  display: flex;
  align-items: center;
  width: 100%;
  height: 50px;
  background-color: transparent;
  position: fixed;
  z-index: 2;
  transition: background-color 0.2s ease-in;
}

.scrolled{
  background-color: var(--primary-bg);
}

.links{
  margin: 0 auto;
  flex: 1 1 auto;
  display: inline-flex;
  height: 100%;
  max-width: 700px;
  justify-content: space-evenly;
  align-items: center;
}

/* Credit to: Barrett Sonntag for filter calculator: https://codepen.io/sosuke/pen/Pjoqqp */
nav img{
  height: 80%;
  margin-left: 5px;
}

nav a{
  color: var(--secondary-text);
  font-weight: 700;
  font-size: 1.2em;
  text-decoration: none;
  letter-spacing: 1px;
  transition: transform 0.1s ease-out, border-bottom 0.1s ease-out;
  text-align: center;
  display: inline-block;
  padding: 5px 0;
}

#nav-highlight{
  width: 200px;
  height: 40px;
  border: 2px solid var(--secondary-text);
  border-radius: 15px;
  visibility: hidden;
  position: absolute;
  transition: all 0.5s ease;
}

nav a:hover{
  border-bottom: 2px solid var(--secondary-text);
  transform: scale(1.1);
}

nav a:active{
  transition: border 0.0s linear;
  color: var(--tertiary-text);
  border-color: var(--tertiary-text);
}
