
.welcome {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  gap: 1em;
  align-items: center;
  justify-content: center;
}

.welcome h1{
  font-size: clamp(3em, 10vw, 8em);
  font-weight: 600;
  margin-bottom: 0.2em;
  letter-spacing: 0.8rem;
}

.welcome p{
  font-size: clamp(0.9em, 2.5vw, 2em);
  width: fit-content;
  margin: 0 auto;
}
.smaller{
  font-size: clamp(0.675em, 1.875vw, 1.5em);
}