.sm-link{
  display: inline-block;
  width: 25px;
  height: 25px;
  margin: 1em;
}
footer{
  width: 100%;
  height: var(--footer-height);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5em;
  background-color: var(--primary-bg)
}
.contact-info{
  line-height: 1.5rem;
}
.contact-info > a{
  color: var(--primary-text);
  text-decoration: none;
}
.contact-info > a:hover{
  color: var(--secondary-text);
  border-bottom: 2px solid var(--secondary-text);
}
.contact-info > a:active{
  color: var(--tertiary-text);
  border-color: var(--tertiary-text);
}
.sm-icon{
  height: 100%;
  width: 100%;
  filter: invert(79%) sepia(7%) saturate(3180%) hue-rotate(107deg) brightness(90%) contrast(83%);
}

.sm-icon:hover{
  animation: shake 0.82s cubic-bezier(.36,.07,.19,.97);
}
.sm-icon:active{
  filter: invert(39%) sepia(60%) saturate(1320%) hue-rotate(249deg) brightness(85%) contrast(86%);
}

@keyframes shake {
  10%, 90% {
    transform: translateX(-1px);
  }
  
  20%, 80% {
    transform: translateX(2px);
  }

  30%, 50%, 70% {
    transform: translateX(-3px);
  }

  40%, 60% {
    transform: translateX(3px);
  }
}