.mosaic{
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  width: 80%;
  max-width: 712px;
  min-width: 350px;
}
.hexagon{
  margin: 0 5px -50px;
  pointer-events: none;
  transition: all 0.1s ease;
}
.hex-path{
  transition: all 0.1s ease;
}
.hexagon a{
  pointer-events: fill;
}
.faded{
  filter: grayscale(100%) blur(1px) contrast(65%) brightness(120%);
}
.scaled{
  transform: scale(1.07);
}
@media only screen and (max-width: 615px){
  .mosaic-wrap{
    width: 350px;
  }
  .hexagon:nth-child(2n){
    margin-left: 123.5px;
  }
}

@media only screen and (min-width: 615px) and (max-width: 915px){
  .mosaic-wrap{
    width: 475px;
  }
  .hexagon:first-child, .hexagon:nth-child(4), .hexagon:nth-child(7){
    margin-left: 123.5px
  }
  .hexagon:last-child{
    margin-bottom: 0;
  }
}

@media only screen and (min-width: 915px){
  .hexagon:first-child, .hexagon:nth-child(6){
    margin-left: 123.5px;
  }
  .hexagon:nth-child(6), .hexagon:last-child{
    margin-bottom: 0;
  }

}

