.proj-name{
  transform: rotate(26.25deg);
  font-size: 3em;
  font-weight: 600;
  fill: var(--primary-text);
  text-shadow: 0 0 1px black, 0 0 1px black, 0 0 1px black,0 0 1px black, 0 0 1px black, 0 0 1px black;
  transition: filter 0.25s ease-in-out;
  filter: opacity(0%);
}

.proj-tech{
  transform: rotate(-26.25deg);
  font-size: 3em;
  font-weight: 400;
  fill: var(--secondary-text);
  text-shadow: 0 0 1px black, 0 0 1px black, 0 0 1px black,0 0 1px black, 0 0 1px black, 0 0 1px black;
  transition: filter 0.25s ease-in-out;
  filter: opacity(0%);
}

.dissolve{
  filter: opacity(100%);
}
path.dissolve{
  filter: opacity(70%);
}

.text-background{
  transition: filter 0.25s ease-in-out;
  filter:opacity(0%)
}