.portrait{
  width: clamp(200px, 100%, 400px);
  mask: url("../assets/hex-bdr-fill.svg") no-repeat center / contain;
  height: 303px;
  width: 269px;
  
}
#about--portrait{
  animation: flip-over-show 1s linear 1s forwards paused;
  visibility: hidden;
}
#about--logo{
  animation: flip-over-hide 1s linear 1s forwards paused;
  visibility: visible;
  position: absolute;
  top: 0%;
  left: 0%;
  z-index: 1;
}
#images-wrapper{
  position: relative;
}

.about{
  display: flex;
  flex-direction: row-reverse;
  min-height: 100vh;
  width: 80%;
  margin: 0 auto;
  gap: 5rem;
  line-height: 1.75;
  align-items: center;
}

#about--text{
  font-size: 1em;
  filter: opacity(0%);
  text-align: justify;
  align-self: center;
  width: 80%;
  animation: fade-in-text 2.5s linear forwards paused;
}


@media only screen and (min-width: 516px) and (max-width: 973px){
  .about{
    display: block;
    color: red;
  }
  #images-wrapper{
    float: right;
    max-width: 263px;
    margin-left: 2em;
  }
  #about--text{
    width: 100%;
  }
}

@media (width <= 515px){
  .about{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1em;
  }
  #about--text{
    width: 100%;
  }
  #images-wrapper{
    width: 80%;
  }
}

@keyframes flip-over-show {
  0%{
    visibility: hidden;
    transform: rotate3d(0,1,0, 0deg);
  }
  10%{
    transform: rotate3d(0, 1, 0, 18deg);
  }
  20%{
    transform: rotate3d(0, 1, 0, 36deg);
  }
  30%{
    transform: rotate3d(0, 1, 0, 54deg);
  }
  40%{
    transform: rotate3d(0, 1, 0, 72deg);
  }
  50%{
    visibility: hidden;
    transform: rotate3d(0, 1, 0, 90deg);
  }
  60%{
    transform: rotate3d(0, 1, 0, 108deg);
  }
  70%{
    transform: rotate3d(0, 1, 0, 126deg);
  }
  80%{
    transform: rotate3d(0, 1, 0, 144deg);
  }
  90%{
    transform: rotate3d(0, 1, 0, 162deg);
  }
  100%{
    visibility: visible;
    transform: rotate3d(0, 1, 0, 180deg);
  }
}

@keyframes flip-over-hide {
  0%{
    visibility: visible;
    transform: rotate3d(0,1,0, 0deg);
  }
  10%{
    transform: rotate3d(0, 1, 0, 18deg);
  }
  20%{
    transform: rotate3d(0, 1, 0, 36deg);
  }
  30%{
    transform: rotate3d(0, 1, 0, 54deg);
  }
  40%{
    transform: rotate3d(0, 1, 0, 72deg);
  }
  50%{
    visibility: hidden;
    transform: rotate3d(0, 1, 0, 90deg);
  }
  60%{
    transform: rotate3d(0, 1, 0, 108deg);
  }
  70%{
    transform: rotate3d(0, 1, 0, 126deg);
  }
  80%{
    transform: rotate3d(0, 1, 0, 144deg);
  }
  90%{
    transform: rotate3d(0, 1, 0, 162deg);
  }
  100%{
    visibility: hidden;
    transform: rotate3d(0, 1, 0, 180deg);
  }
}

@keyframes fade-in-text{
 from{
  filter: opacity(0%);
 }
 to{
  filter: opacity(100%);
 }
}