
.credential{
  display: flex;
  min-height: 50px;
  width: fit-content;
  margin-left: 50%;
  margin-right: auto;
  align-items: center;
  gap: 1em;
}

.credential img{
  width: 50px;
  max-height: 40px;
  margin-left: 1em;
  fill: white;
}
.credential.even img{
  margin-right: 1em;
  margin-left: 0;
}

.credential p{
  width: fit-content;
  padding: 0.25em;
}

.credential.even{
  flex-direction: row-reverse;
  text-align: right;
  margin-right: 50%;
  margin-left: auto;
  justify-content: flex-start;
}